import { useLocation, useNavigate } from "react-router-dom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import {
  useInoutwardApprovedOutward,
  useInoutwardOutwardInwardDetail,
  useInoutwardRejectOutward,
} from "../../../Hooks/ApiHooks/InOutwardManager/inoutward-manager-api";
import styleFeedback from "../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
import { pdfCdnUrl } from "../../../Services/UrlConfig/BaseUrl";
import { Fragment, useState } from "react";
import FormFileView from "../../../Ui/Input/FormFileView";
import QvipleLoader from "../../../Loader/QvipleLoader";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import CreateButton from "../../../Ui/Button/CreateButton";

const InOutwardNotifyDetail = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const [disabled, setDisabled] = useState(false);
  const [inoutwardApprovedOutward] = useInoutwardApprovedOutward();
  const [inoutwardRejectOutward] = useInoutwardRejectOutward();
  const {
    inoutwardOutwardInwardDetail,
    inoutwardOutwardInwardDetailLoading,
    inoutwardOutwardInwardDetailRefetch,
  } = useInoutwardOutwardInwardDetail({
    data: {
      oid: getQuery.state?.oid,
    },
    skip: !getQuery.state?.oid,
  });
  const onBack = () => {
    navigate(-1);
  };

  const onApproved = (e) => {
    if (getQuery.state?.oid && getQuery.state?.staffId) {
      setDisabled((pre) => !pre);
      inoutwardApprovedOutward({
        sid: getQuery.state?.staffId,
        oid: getQuery.state?.oid,
      })
        .then(() => {
          inoutwardOutwardInwardDetailRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onReject = (e) => {
    if (getQuery.state?.oid && getQuery.state?.staffId) {
      setDisabled((pre) => !pre);
      inoutwardRejectOutward({
        sid: getQuery.state?.staffId,
        oid: getQuery.state?.oid,
      })
        .then(() => {
          inoutwardOutwardInwardDetailRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>
              {inoutwardOutwardInwardDetail?.subject ??
                inoutwardOutwardInwardDetail?.name ??
                ""}
              {" - "}{" "}
              <span
                style={{
                  fontSize: "13px",
                }}
              >
                (
                {inoutwardOutwardInwardDetail?.model_type === "INWARD"
                  ? "Inward"
                  : "Outward"}
                )
              </span>
            </h6>

            <div className={style.fm_search}>
              {inoutwardOutwardInwardDetail?.generated_report ? (
                <a
                  href={`${pdfCdnUrl}/${inoutwardOutwardInwardDetail?.generated_report}`}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img
                    src={`${assestsUserFinanceUrl}/receipt.svg`}
                    alt="receipt icon"
                    style={{
                      width: "1.5rem",
                      height: "1.5rem",
                    }}
                  />
                </a>
              ) : null}
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onBack}
                style={{
                  height: "1.5rem",
                  width: "1.5rem",
                }}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          {inoutwardOutwardInwardDetail?.model_type === "INWARD" ? (
            <div className={styleFeedback.sf_card_contatiner_inner}>
              <h6
                style={{
                  marginBottom: "0.3rem",
                }}
              >
                {t("name")}
                {": "}
                <span
                  style={{
                    fontWeight: "400",
                  }}
                >
                  {inoutwardOutwardInwardDetail?.name ?? ""}
                </span>
              </h6>
              <h6
                style={{
                  marginBottom: "0.3rem",
                }}
              >
                {t("no")}
                {": "}
                <span
                  style={{
                    fontWeight: "400",
                  }}
                >
                  {inoutwardOutwardInwardDetail?.number ?? ""}
                </span>
              </h6>
              <h6>
                {t("date")}
                {": "}
                <span
                  style={{
                    fontWeight: "400",
                  }}
                >
                  {dayjs(inoutwardOutwardInwardDetail?.date)?.format(
                    "DD/MM/YYYY"
                  ) ?? ""}
                </span>
              </h6>
              <h6
                style={{
                  marginBottom: "0.3rem",
                }}
              >
                {t("type")}
                {": "}
                <span
                  style={{
                    fontWeight: "400",
                  }}
                >
                  {inoutwardOutwardInwardDetail?.outward_type ?? ""}
                </span>
              </h6>
              {getQuery.state?.isApproved === "YES" ? (
                <h6
                  style={{
                    marginBottom: "0.3rem",
                  }}
                >
                  {t("prepared_by")}
                  {": "}
                  <span
                    style={{
                      fontWeight: "400",
                    }}
                  >
                    {`${
                      inoutwardOutwardInwardDetail?.prepare_by
                        ?.staffFirstName ?? ""
                    } ${
                      inoutwardOutwardInwardDetail?.prepare_by
                        ?.staffMiddleName ?? ""
                    } ${
                      inoutwardOutwardInwardDetail?.prepare_by?.staffLastName ??
                      ""
                    }`}
                  </span>
                </h6>
              ) : null}

              {inoutwardOutwardInwardDetail?.image?.length > 0 ? (
                <Fragment>
                  <h6
                    style={{
                      marginBlock: "1rem 0.5rem",
                    }}
                  >
                    {t("image")}
                  </h6>
                  {inoutwardOutwardInwardDetail?.image?.map((dt) => (
                    <FormFileView
                      key={dt}
                      label={`${t("image")} :`}
                      value={{
                        key: dt,
                      }}
                      customFormInputContainer={{
                        marginBlock: "0",
                      }}
                    />
                  ))}
                </Fragment>
              ) : null}

              {inoutwardOutwardInwardDetail?.attachment?.length > 0 ? (
                <Fragment>
                  <h6
                    style={{
                      marginBlock: "1rem 0.5rem",
                    }}
                  >
                    {t("attachment")}
                  </h6>
                  {inoutwardOutwardInwardDetail?.attachment?.map((dt) => (
                    <FormFileView
                      key={dt}
                      label={`${t("attachment")} :`}
                      value={{
                        key: dt,
                      }}
                      customFormInputContainer={{
                        marginBlock: "0",
                      }}
                    />
                  ))}
                </Fragment>
              ) : null}

              {getQuery.state?.isApproved === "YES" ? null : (
                <>
                  <h6
                    style={{
                      marginBlock: "1rem 0.5rem",
                    }}
                  >
                    {t("approval_for")}
                  </h6>

                  {inoutwardOutwardInwardDetail?.approvals_for?.map(
                    (ap, index) => (
                      <h6
                        key={`${index}${ap?.staff?._id}`}
                        style={{
                          fontWeight: "400",
                        }}
                      >
                        {`${ap?.staff?.staffFirstName ?? ""} ${
                          ap?.staff?.staffMiddleName ?? ""
                        } ${ap?.staff?.staffLastName ?? ""}`}
                        {" - "}
                        {ap?.status === "Pending" ? (
                          <span
                            style={{
                              color: "rgb(231, 75, 75)",
                            }}
                          >
                            ({`${ap?.status ?? ""}`})
                          </span>
                        ) : (
                          <span
                            style={{
                              color: "#00f",
                            }}
                          >
                            ({`${ap?.status ?? ""}`})
                          </span>
                        )}
                      </h6>
                    )
                  )}
                </>
              )}
            </div>
          ) : (
            <div className={styleFeedback.sf_card_contatiner_inner}>
              <h6
                style={{
                  marginBottom: "0.3rem",
                }}
              >
                {t("outward_no")}
                {": "}
                <span
                  style={{
                    fontWeight: "400",
                  }}
                >
                  {inoutwardOutwardInwardDetail?.outward_number ?? ""}
                </span>
              </h6>
              <h6
                style={{
                  marginBottom: "0.3rem",
                }}
              >
                {t("subject_only")}
                {": "}
                <span
                  style={{
                    fontWeight: "400",
                  }}
                >
                  {inoutwardOutwardInwardDetail?.subject ?? ""}
                </span>
              </h6>
              <h6
                style={{
                  marginBottom: "0.3rem",
                }}
              >
                {t("type")}
                {": "}
                <span
                  style={{
                    fontWeight: "400",
                  }}
                >
                  {inoutwardOutwardInwardDetail?.outward_type ?? ""}
                </span>
              </h6>

              <h6
                style={{
                  whiteSpace: "break-spaces",
                  fontWeight: "400",
                }}
              >
                <span
                  style={{
                    fontWeight: "500",
                  }}
                >
                  {t("body")}
                </span>

                {": "}
                {inoutwardOutwardInwardDetail?.body ?? ""}
              </h6>

              {getQuery.state?.isApproved === "YES" ? (
                <h6
                  style={{
                    marginBottom: "0.3rem",
                  }}
                >
                  {t("prepared_by")}
                  {": "}
                  <span
                    style={{
                      fontWeight: "400",
                    }}
                  >
                    {`${
                      inoutwardOutwardInwardDetail?.prepare_by
                        ?.staffFirstName ?? ""
                    } ${
                      inoutwardOutwardInwardDetail?.prepare_by
                        ?.staffMiddleName ?? ""
                    } ${
                      inoutwardOutwardInwardDetail?.prepare_by?.staffLastName ??
                      ""
                    }`}
                  </span>
                </h6>
              ) : null}

              {inoutwardOutwardInwardDetail?.image?.length > 0 ? (
                <Fragment>
                  <h6
                    style={{
                      marginBlock: "1rem 0.5rem",
                    }}
                  >
                    {t("image")}
                  </h6>
                  {inoutwardOutwardInwardDetail?.image?.map((dt) => (
                    <FormFileView
                      key={dt}
                      label={`${t("image")} :`}
                      value={{
                        key: dt,
                      }}
                      customFormInputContainer={{
                        marginBlock: "0",
                      }}
                    />
                  ))}
                </Fragment>
              ) : null}

              {inoutwardOutwardInwardDetail?.attachment?.length > 0 ? (
                <Fragment>
                  <h6
                    style={{
                      marginBlock: "1rem 0.5rem",
                    }}
                  >
                    {t("attachment")}
                  </h6>
                  {inoutwardOutwardInwardDetail?.attachment?.map((dt) => (
                    <FormFileView
                      key={dt}
                      label={`${t("attachment")} :`}
                      value={{
                        key: dt,
                      }}
                      customFormInputContainer={{
                        marginBlock: "0",
                      }}
                    />
                  ))}
                </Fragment>
              ) : null}

              {getQuery.state?.isApproved === "YES" ? null : (
                <>
                  <h6
                    style={{
                      marginBlock: "1rem 0.5rem",
                    }}
                  >
                    {t("approval_for")}
                  </h6>

                  {inoutwardOutwardInwardDetail?.approvals_for?.map(
                    (ap, index) => (
                      <h6
                        key={`${index}${ap?.staff?._id}`}
                        style={{
                          fontWeight: "400",
                        }}
                      >
                        {`${ap?.staff?.staffFirstName ?? ""} ${
                          ap?.staff?.staffMiddleName ?? ""
                        } ${ap?.staff?.staffLastName ?? ""}`}
                        {" - "}
                        {ap?.status === "Pending" ? (
                          <span
                            style={{
                              color: "rgb(231, 75, 75)",
                            }}
                          >
                            ({`${ap?.status ?? ""}`})
                          </span>
                        ) : (
                          <span
                            style={{
                              color: "#00f",
                            }}
                          >
                            ({`${ap?.status ?? ""}`})
                          </span>
                        )}
                      </h6>
                    )
                  )}
                </>
              )}
            </div>
          )}
          {getQuery.state?.isApproved === "YES" ? (
            <div className={style.fm_header_container}>
              <CreateButton
                label="approve"
                customStyle={{
                  padding: "0.3rem 1rem",
                  width: "49%",
                }}
                onAction={onApproved}
              />
              {inoutwardOutwardInwardDetail?.cancle_by_staff?.includes(
                getQuery.state?.staffId
              ) ? null : (
                <CreateButton
                  label="reject"
                  customStyle={{
                    padding: "0.3rem 1rem",
                    width: "49%",
                  }}
                  onAction={onReject}
                />
              )}
            </div>
          ) : null}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {inoutwardOutwardInwardDetailLoading && <QvipleLoader />}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default InOutwardNotifyDetail;
